/* App.css */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #f8f9fa;
  color: #333;
}

.app-container {
  padding: 1rem;
}

.app-header {
  font-size: 1.75rem;
  font-weight: 600;
  text-align: center;
  margin-bottom: 1rem;
}

.msr-title {
  color: #0056d6;
}

.search-bar {
  display: block;
  width: 100%;
  max-width: 400px;
  margin: 0 auto 1rem auto;
  padding: 0.5rem 0.75rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 6px;
}

.table-container {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  touch-action: pan-y;
}

.leaderboard-table {
  width: 100%;
  min-width: 900px;
  border-collapse: collapse;
}

.leaderboard-table th {
  background-color: #0056d6;
  color: #fff;
  position: sticky;
  top: 0;
  z-index: 10;
  padding: 10px;
  text-align: left;
  font-size: 0.9rem;
  cursor: pointer;
  white-space: nowrap;
}

.leaderboard-table td {
  padding: 10px;
  border-bottom: 1px solid #ddd;
  text-align: left;
  white-space: nowrap;
  font-size: 0.88rem;
}

.highlight-msr {
  background-color: #0056d6;
  color: white;
  font-weight: 600;
}

.leaderboard-table tr:hover {
  background-color: #f1f1f1;
}

/* Mobile fixes */
@media (max-width: 768px) {
  .leaderboard-table {
    font-size: 0.8rem;
  }

  .leaderboard-table th {
    position: static; /* Removes stickiness to avoid scroll conflict */
  }

  .search-bar {
    width: 90%;
  }
}
















